
import { BreakpointsLabel } from "../utils/breakpoins";
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: {
    type: {
      type: String,
      default: "default",
      validator: (value: string): boolean => {
        return [
          "default",
          "action",
          "link",
          "pseudo",
          "clear",
          "raised",
        ].includes(value);
      },
    },
    size: {
      type: String,
      default: "m",
      validator: (value: string): boolean => {
        return BreakpointsLabel.includes(value);
      },
    },
    round: {
      type: Boolean,
      default: false,
    },
    rounded: {
      type: Boolean,
      default: false,
    },
    mode: {
      type: String,
      default: "",
    },
    badge: {
      type: String,
      default: "",
      required: false,
    },
    badgeType: {
      type: String,
      required: false,
      default: "edge",
      validator: (value: string): boolean => {
        return ["edge", "inside"].includes(value);
      },
    },
    badgeColor: {
      type: String,
      required: false,
      validator: (value: string): boolean => {
        return ["primary", "secondary", "accent", "success", "danger"].includes(
          value
        );
      },
    },
    padding: {
      type: Number,
      required: false,
    },
  },
  setup(props, { emit }) {
    const isActive = ref(false);

    const toggleState = () => {
      isActive.value = !isActive.value;

      setTimeout(() => {
        isActive.value = false;
      }, 400);

      emit("click");
    };

    return { isActive, toggleState };
  },
  emits: ["click"],
});
