
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    type: {
      type: String,
      required: true,
      validator: (value: string): boolean => {
        return ["primary", "secondary", "accent"].includes(value);
      },
    },
    mode: {
      type: String,
      required: true,
      validator: (value: string): boolean => {
        return ["border", "growing"].includes(value);
      },
    },
  },
});
