
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    container: {
      type: Boolean,
      required: false,
      default: false,
    },
    align: {
      type: String,
      required: false,
      validator: (value: string): boolean => {
        return ["start", "end", "center"].includes(value);
      },
    },
  },
});
