
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: {
    label: {
      type: String,
      required: true,
    },
    validationPattern: {
      type: RegExp,
      required: false,
    },
    validationMessage: {
      type: String,
      required: false,
      default: "",
    },
    inputType: {
      type: String,
      default: "text",
      validator: (value: string): boolean => {
        return ["text", "tel", "email", "password"].includes(value);
      },
    },
    modelValue: {
      type: String,
      required: false,
      default: "",
    },
    inputRequired: {
      type: Boolean,
      default: false,
      required: false,
    },
    inputAutofocus: {
      type: Boolean,
      required: false,
      default: false,
    },
    inputReadonly: {
      type: Boolean,
      required: false,
      default: false,
    },
    inputCursor: {
      type: String,
      required: false,
      default: "text",
      validator: (value: string): boolean => {
        return ["text", "pointer"].includes(value);
      },
    },
    maxLength: {
      type: [Number, String],
      validator: (value: number | string): boolean => {
        if (typeof value === "string") {
          return /^\d+$/.test(value);
        } else {
          return true;
        }
      },
    },
    mask: {
      type: String,
      required: false,
      default: "",
    },
  },
  directives: {
    autofocus: {
      mounted: (element: HTMLElement): void => {
        console.log(element);
        element.focus();
      },
    },
  },
  setup(props, { emit }) {
    const content = ref(props.modelValue);
    const isActive = ref(false);
    const validatorStatus = ref(true);

    if (content.value.length > 0) {
      isActive.value = true;
    }

    const toggleActive = (): void => {
      if (content.value.length < 1) {
        isActive.value = !isActive.value;
      }
    };

    const inputFocus = (): void => {
      toggleActive();
    };

    const inputBlur = (): void => {
      toggleActive();

      if (!props.validationMessage || !props.validationPattern) return;

      inputValidate();
    };

    const inputValidate = (): void => {
      if (props.validationPattern) {
        validatorStatus.value = props.validationPattern.test(content.value);
      }
    };

    const inputUpdate = (value: string): void => {
      content.value = value;
      emit("update:modelValue", value);
    };

    return {
      isActive,
      validatorStatus,
      content,
      inputFocus,
      inputBlur,
      inputUpdate,
    };
  },
  emits: ["click", "update:modelValue"],
});
