
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: {
    transition: {
      type: String,
      default: "fade",
      validator: (value: string): boolean => {
        return ["fade", "slide"].includes(value);
      },
    },
  },
  setup() {
    const isActive = ref(false);
    return { isActive };
  },
  emits: ["click"],
});
