
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: {
    icon: {
      type: String,
      required: false,
    },
  },
  setup() {
    const isActive = ref(false);
    const onCheckboxItemClicked = () => {
      isActive.value = !isActive.value;
    };

    return { isActive, onCheckboxItemClicked };
  },
});
