
import { BreakpointsLabel } from "../utils/breakpoins";
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    size: {
      type: String,
      require: true,
      default: "xl",
      validator: (value: string): boolean => {
        return [...BreakpointsLabel, "xxl"].includes(value);
      },
    },
    align: {
      type: String,
      required: false,
      default: "center",
    },
  },
});
