
import Icon from "./Icon.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: {
    Icon,
  },
  props: {
    color: {
      type: String,
      required: false,
      default: "primary",
      validator: (value: string): boolean => {
        return ["primary", "secondary", "accent"].includes(value);
      },
    },
    max: {
      type: Number,
      required: false,
    },
    iconName: {
      type: String,
      required: true,
    },
    iconColor: {
      type: String,
      required: false,
    },
    iconSize: {
      type: String,
      default: "m",
    },
    iconWeight: {
      type: String,
      default: "normal",
      required: false,
    },
  },
});
