
import { directive } from "vue3-click-away";
import { defineComponent, ref } from "vue";

export default defineComponent({
  directives: {
    ClickAway: directive,
  },
  props: {
    direction: {
      type: String,
      required: false,
      default: "bottom-left",
      validator: (value: string): boolean => {
        return [
          "bottom-left",
          "bottom-center",
          "bottom-right",
          "top-left",
          "top-center",
          "top-right",
        ].includes(value);
      },
    },
    hover: {
      type: Boolean,
      required: false,
      default: false,
    },
    maxWidth: {
      type: Number,
      required: false,
    },
    clearly: {
      type: Boolean,
      required: false,
      default: false,
    },
    fullView: {
      type: Boolean,
      required: false,
      default: false,
    },
    closeOnClick: {
      type: Boolean,
      required: false,
      default: false,
    },
    modelValue: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const isActive = ref(false);
    const isHover = ref(false);

    const onDropdownToggleClicked = () => {
      if (!props.hover) {
        isActive.value = !isActive.value;
        emit("update:modelValue", isActive.value);
      }
    };
    const onDropdownAwayClicked = () => {
      if (!props.hover) {
        isActive.value = false;
        emit("update:modelValue", isActive.value);
      }
    };
    const onDropdownMouseOver = () => {
      if (props.hover) {
        isActive.value = true;
        emit("update:modelValue", isActive.value);
      }
    };
    const onDropdownMouseLeave = () => {
      if (props.hover) {
        setTimeout(() => {
          if (!isHover.value) {
            isActive.value = false;
          }
        }, 500);
      }
    };

    const onDropdownMouseOverOnItem = () => {
      if (props.hover) {
        isHover.value = true;
      }
    };

    const onDropdownMouseLeaveFromItem = () => {
      if (props.hover) {
        isHover.value = false;
      }
    };

    return {
      isActive,
      isHover,
      onDropdownToggleClicked,
      onDropdownAwayClicked,
      onDropdownMouseOver,
      onDropdownMouseLeave,
      onDropdownMouseOverOnItem,
      onDropdownMouseLeaveFromItem,
    };
  },
  emits: ["update:modelValue"],
});
