
import { BreakpointsLabel } from "../utils/breakpoins";
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    size: {
      type: String,
      required: true,
      default: "s",
      validator: (value: string): boolean => {
        return BreakpointsLabel.includes(value);
      },
    },
    align: {
      type: String,
      required: false,
      default: "center",
    },
    type: {
      type: String,
      required: false,
      default: "shadow",
    },
    hoverable: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
});
